import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { RouterModule, Routes, PreloadAllModules } from "@angular/router";
import { ComposeMessageComponent } from "./compose-message/compose-message.component";
import { CrisisListComponent } from "./crisis-list/crisis-list.component";
import { PageNotFoundComponent } from "./page-not-found/page-not-found.component";
import { AuthGuard } from "./auth/auth.guard";
import { SelectivePreloadingStrategyService } from "./selective-preloading-strategy.service";

const appRoutes: Routes = [
  {
    path: "admin",
    loadChildren: () => import("./admin/admin.module").then(m => m.AdminModule),
    canLoad: [AuthGuard]
  },
  {
    path: "crisis-center",
    loadChildren: () =>
      import("./crisis-center/crisis-center.module").then(
        m => m.CrisisCenterModule
      ),
    data: {
      preload: true
    }
  },
  // 配置第二路由
  {
    path: "compose",
    component: ComposeMessageComponent,
    outlet: "popup"
  },
  // 默认路由，当什么路径都不输入的时候跳到这里
  /* 这里pathMatch的意思其实就是全匹配和部分匹配的意思，如果设置为prefix，那么只要路由有部分匹配到了''，就会跳到heroes，
  很明显这样的话所有路由都会跳到heroes，为full的话只有整个路径都和''一样才会重定向，soga */
  {
    path: "",
    redirectTo: "/superheroes",
    pathMatch: "full"
  },
  // 通配路由，当路由都匹配不上的时候会跳到这里
  {
    path: "**",
    component: PageNotFoundComponent
  }
];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forRoot(appRoutes, {
      enableTracing: false,
      // preloadingStrategy: PreloadAllModules
      preloadingStrategy: SelectivePreloadingStrategyService
    })
  ],
  // 放在providers中以便可以注入应用的任何地方
  providers: [SelectivePreloadingStrategyService],
  exports: [RouterModule]
})
export class AppRoutingModule {}
